import { mapState } from "vuex";

export default {
    name: 'app-order',
    props: {
        item: {
            type: Object
        },
        is_user: {
            type: Boolean
        },
        is_confirm: {
            type: Boolean
        },
        theme: Object
    },
    data() {
        return {
            show: false,
            orderList: [],
            id: 0,
        };
    },
    methods: {
        toRemind(item) {
            if(item.id > 0) {
                this.show = true;
                this.id = item.id
            }else {
                this.show = false;
                this.id = 0;
            }
        },
        // 订单支付
        orderPay(e) {
            let _this = this;
            _this.$showLoading();
            _this.$request({
                url: _this.$api.order.list_pay_data,
                data: {
                    id: e.id
                }
            }).then(response => {
                _this.$hideLoading();
                if (response.code === 0) {
                    _this.$payment.pay(response.data.id).then(() => {
                        // 支付成功
                        uni.redirectTo({
                            url: '/plugins/community/order/order?is_user=1'
                        })
                    }).catch(e => {
                        // 支付失败
                        uni.showModal({
                            title: '',
                            content: e.errMsg,
                            showCancel: false
                        })
                    })
                }
            }).catch(() => {
                _this.$hideLoading();
            });
        },
        toDetail(id) {
            let is_user = 0;
            if(this.is_user) {
                is_user = 1
            }
            uni.navigateTo({
                url: '/plugins/community/order-detail/order-detail?id=' + id + '&is_user=' + is_user
            });
        },
        beConfirm() {
            let that = this;
            that.show = false;
            uni.showLoading({
                mask: true,
                title: '确认提货中...'
            });
            that.$request({
                url: that.is_user ? that.$api.order.confirm : that.$api.community.confirm,
                data: {
                    id: that.id
                }
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.item.is_confirm = 1;
                    that.id = 0;
                    that.$emit('update', that.item);
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            })
        }
    }
}
